.slide:nth-child(1){
    left: 0;
}
.slide:nth-child(2){
    left: 100%;
}
.slide:nth-child(3){
    left: 200%;
}
.slide:nth-child(4){
    left: 300%;
}
.slide:nth-child(5){
    left: 400%;
}

@media screen and (max-width: 670px){
    .slider-conteiner .slide img{
        object-fit: fill;
    }
}